/* ============================================
   Native-Grade Mobile Design System
   ============================================ */

:root {
  --safe-area-top: env(safe-area-inset-top, 0px);
  --safe-area-bottom: env(safe-area-inset-bottom, 0px);
  --safe-area-left: env(safe-area-inset-left, 0px);
  --safe-area-right: env(safe-area-inset-right, 0px);

  --mobile-nav-height: 60px;
  --mobile-bottom-nav-height: 68px;
  --mobile-touch-size: 48px;
  --mobile-radius-sm: 12px;
  --mobile-radius-md: 16px;
  --mobile-radius-lg: 20px;
  --mobile-radius-xl: 24px;
  --mobile-shadow-soft: 0 18px 38px rgba(15, 23, 42, 0.12);
  --mobile-shadow-strong: 0 24px 52px rgba(15, 23, 42, 0.18);
  --mobile-transition: 180ms cubic-bezier(0.34, 1.56, 0.64, 1);
}

@media (max-width: 1024px) {
  /* Viewport & layout */
  * {
    box-sizing: border-box;
  }

  html {
    width: 100%;
    min-height: 100%;
    overflow-x: hidden;
    -webkit-text-size-adjust: 100%;
    text-size-adjust: 100%;
  }

  body {
    margin: 0;
    width: 100%;
    min-height: 100vh;
    min-height: 100dvh;
    overflow-x: hidden;
    background: radial-gradient(120% 160% at 100% 0%, rgba(59, 130, 246, 0.08), transparent 55%),
      radial-gradient(140% 160% at 0% 0%, rgba(139, 92, 246, 0.08), transparent 55%),
      linear-gradient(180deg, rgba(248, 250, 252, 0.92), rgba(255, 255, 255, 0.98));
  }

  #mobile-app-root {
    flex: 1;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    min-height: 100dvh;
    padding-top: calc(var(--safe-area-top) + var(--mobile-nav-height));
    padding-bottom: calc(var(--mobile-bottom-nav-height) + var(--safe-area-bottom));
    position: relative;
    background: transparent;
    isolation: isolate;
  }

  #mobile-app-root > * {
    max-width: 100%;
  }

  main,
  .main-content,
  .content-area,
  .dashboard-layout,
  .page-wrapper,
  .page-content {
    width: 100%;
    max-width: 100%;
    padding-left: clamp(1rem, 4vw, 1.5rem);
    padding-right: clamp(1rem, 4vw, 1.5rem);
  }

  .container,
  [class*="container"] {
    width: 100%;
    max-width: 100%;
    padding-left: clamp(1rem, 4vw, 1.5rem);
    padding-right: clamp(1rem, 4vw, 1.5rem);
    margin-left: auto;
    margin-right: auto;
  }

  /* Remove desktop sidebars */
  aside,
  nav.sidebar,
  .sidebar,
  [class*="sidebar"] {
    display: none !important;
  }

  #root,
  .App {
    overflow-x: hidden;
    width: 100%;
    max-width: 100%;
  }

  .mobile-only {
    display: block !important;
  }

  .desktop-only {
    display: none !important;
  }

  /* Scroll containers */
  .mobile-scroll-container {
    flex: 1;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    scroll-behavior: smooth;
    overscroll-behavior-y: contain;
  }

  .mobile-scroll-container::-webkit-scrollbar {
    display: none;
  }

  .mobile-scroll-container {
    scrollbar-width: none;
  }

  /* Touch feedback */
  .mobile-touch-feedback {
    transition: transform var(--mobile-transition), box-shadow var(--mobile-transition),
      background-color 120ms ease;
  }

  .mobile-touch-feedback:active {
    transform: scale(0.97);
  }

  .mobile-touch-feedback:focus-visible {
    outline: 3px solid rgba(59, 130, 246, 0.35);
    outline-offset: 2px;
  }

  .mobile-ripple {
    position: relative;
    overflow: hidden;
  }

  .mobile-ripple::after {
    content: '';
    position: absolute;
    inset: 0;
    border-radius: inherit;
    background: radial-gradient(circle at center, rgba(59, 130, 246, 0.22), transparent 60%);
    opacity: 0;
    transform: scale(0.3);
    transition: opacity 200ms ease, transform 300ms ease;
  }

  .mobile-ripple:active::after {
    opacity: 1;
    transform: scale(1);
  }

  /* Icon buttons */
  .btn-icon-mobile {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: var(--mobile-touch-size);
    height: var(--mobile-touch-size);
    border-radius: 15px;
    border: none;
    background: rgba(15, 23, 42, 0.04);
    color: inherit;
    box-shadow: inset 0 0 0 1px rgba(15, 23, 42, 0.05);
    transition: transform var(--mobile-transition), box-shadow var(--mobile-transition),
      background 140ms ease;
  }

  .btn-icon-mobile:hover {
    background: rgba(59, 130, 246, 0.08);
    box-shadow: inset 0 0 0 1px rgba(59, 130, 246, 0.18);
  }

  .btn-icon-mobile:active {
    transform: scale(0.95);
  }

  /* Buttons */
  button {
    min-height: 44px;
    touch-action: manipulation;
  }

  .btn-mobile {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 0.65rem;
    min-height: var(--mobile-touch-size);
    width: 100%;
    border-radius: var(--mobile-radius-lg);
    font-weight: 600;
    font-size: 0.95rem;
    border: none;
    transition: transform var(--mobile-transition), box-shadow var(--mobile-transition),
      background-color 160ms ease, color 160ms ease;
    text-decoration: none;
    position: relative;
  }

  .btn-mobile:active:not(:disabled) {
    transform: translateY(1px);
  }

  .btn-primary-mobile {
    background: linear-gradient(135deg, #2563eb 0%, #6366f1 100%);
    color: #ffffff;
    box-shadow: 0 12px 32px rgba(99, 102, 241, 0.35);
  }

  .btn-primary-mobile:hover {
    background: linear-gradient(135deg, #1d4ed8 0%, #4f46e5 100%);
  }

  .btn-secondary-mobile {
    background: rgba(59, 130, 246, 0.08);
    color: #1e3a8a;
    box-shadow: inset 0 0 0 1px rgba(59, 130, 246, 0.18);
  }

  .btn-secondary-mobile:hover {
    background: rgba(59, 130, 246, 0.12);
  }

  /* Cards & surfaces */
  .card-elevated {
    border-radius: var(--mobile-radius-xl);
    background: rgba(255, 255, 255, 0.92);
    box-shadow: var(--mobile-shadow-soft);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(15, 23, 42, 0.08);
  }

  .card-mobile {
    border-radius: var(--mobile-radius-lg);
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 0 12px 24px rgba(15, 23, 42, 0.08);
  }

  .loading-spinner-mobile {
    border-radius: 50%;
    border: 2px solid rgba(148, 163, 184, 0.3);
    border-top-color: rgba(59, 130, 246, 0.85);
    animation: spin 0.8s linear infinite;
  }

  .list-mobile {
    background: rgba(255, 255, 255, 0.96);
    border-radius: var(--mobile-radius-xl);
    box-shadow: var(--mobile-shadow-soft);
    overflow: hidden;
    border: 1px solid rgba(15, 23, 42, 0.05);
  }

  .list-item-mobile {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1rem 1.25rem;
    min-height: var(--mobile-touch-size);
    transition: background-color var(--mobile-transition);
  }

  .list-item-mobile + .list-item-mobile {
    border-top: 1px solid rgba(226, 232, 240, 0.8);
  }

  .list-item-mobile:active {
    background: rgba(59, 130, 246, 0.08);
  }

  /* Calendar (react-big-calendar) */
  .rbc-calendar {
    width: 100%;
    max-width: 100%;
    border: none;
    background: transparent;
    font-size: 0.85rem;
  }

  .rbc-toolbar {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    padding: 0.75rem clamp(0.75rem, 4vw, 1rem);
  }

  .rbc-toolbar > span {
    flex: 1 1 30%;
    display: flex;
    justify-content: center;
    min-width: 0;
  }

  .rbc-toolbar button {
    flex: 1;
    min-width: 0;
    font-size: 0.75rem;
    padding: 0.5rem 0.75rem;
    border-radius: var(--mobile-radius-sm);
    border: 1px solid rgba(148, 163, 184, 0.35);
    background: rgba(255, 255, 255, 0.92);
    transition: background var(--mobile-transition), border-color var(--mobile-transition);
  }

  .rbc-toolbar button:active {
    background: rgba(59, 130, 246, 0.12);
    border-color: rgba(59, 130, 246, 0.45);
  }

  .rbc-toolbar-label {
    flex: 1 1 100%;
    text-align: center;
    font-weight: 600;
    font-size: 0.9rem;
    color: #1f2937;
  }

  .rbc-month-view {
    border-radius: var(--mobile-radius-md);
    overflow: hidden;
    box-shadow: var(--mobile-shadow-soft);
    background: rgba(255, 255, 255, 0.95);
  }

  .rbc-month-view table {
    width: 100%;
    table-layout: fixed;
  }

  .rbc-header {
    padding: 0.35rem 0.25rem;
    font-size: 0.65rem;
    text-transform: uppercase;
    letter-spacing: 0.04em;
    color: #64748b;
  }

  .rbc-date-cell {
    padding: 0.25rem;
    font-size: 0.7rem;
  }

  .rbc-event {
    border-radius: 10px;
    padding: 0.2rem 0.4rem;
    font-size: 0.68rem;
    line-height: 1.2;
    box-shadow: 0 8px 16px rgba(15, 23, 42, 0.18);
  }

  .rbc-row-content {
    min-height: 42px;
  }

  .rbc-time-view {
    border-radius: var(--mobile-radius-md);
    overflow: hidden;
    box-shadow: var(--mobile-shadow-soft);
    background: rgba(255, 255, 255, 0.95);
  }

  .rbc-time-header,
  .rbc-time-content {
    border: none;
  }

  .rbc-time-header {
    padding: 0.5rem;
  }

  .rbc-time-content > * + * {
    border-left: 1px solid rgba(226, 232, 240, 0.8);
  }

  /* Header */
  .top-nav-mobile {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: calc(var(--mobile-nav-height) + var(--safe-area-top));
    padding-top: var(--safe-area-top);
    display: flex;
    align-items: center;
    background: rgba(255, 255, 255, 0.94);
    backdrop-filter: blur(24px) saturate(160%);
    border-bottom: 1px solid rgba(15, 23, 42, 0.08);
    z-index: 100;
    padding-left: clamp(1rem, 4vw, 1.5rem);
    padding-right: clamp(1rem, 4vw, 1.5rem);
    box-shadow: 0 10px 30px rgba(15, 23, 42, 0.05);
  }

  .top-nav-mobile .mobile-touch-feedback {
    min-height: var(--mobile-touch-size);
  }

  /* Drawer */
  .mobile-menu-overlay {
    position: fixed;
    inset: 0;
    z-index: 95;
    background: rgba(15, 23, 42, 0.45);
    backdrop-filter: blur(6px);
    animation: fadeIn 160ms ease forwards;
  }

  .mobile-menu {
    position: fixed;
    inset: 0 0 0 auto;
    width: min(340px, 88vw);
    background: rgba(255, 255, 255, 0.98);
    backdrop-filter: blur(32px) saturate(180%);
    border-left: 1px solid rgba(15, 23, 42, 0.06);
    transform: translateX(105%);
    transition: transform var(--mobile-transition);
    display: flex;
    flex-direction: column;
    padding-bottom: calc(var(--safe-area-bottom) + 1.5rem);
    box-shadow: -24px 0 60px rgba(15, 23, 42, 0.18);
  }

  .mobile-menu.open {
    transform: translateX(0);
  }

  .mobile-menu .mobile-scroll-container {
    padding: 0 1rem 1rem;
  }

  /* Bottom navigation */
  .bottom-nav-mobile {
    position: fixed;
    inset: auto 0 0;
    height: calc(var(--mobile-bottom-nav-height) + var(--safe-area-bottom));
    padding: 0.4rem clamp(0.75rem, 4vw, 1.5rem);
    padding-bottom: calc(var(--safe-area-bottom) + 0.6rem);
    display: flex;
    gap: 0.6rem;
    background: rgba(15, 23, 42, 0.92);
    backdrop-filter: blur(24px);
    border-top: 1px solid rgba(148, 163, 184, 0.15);
    box-shadow: 0 -20px 35px rgba(15, 23, 42, 0.3);
    z-index: 90;
  }

  .bottom-nav-item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.25rem;
    min-height: 50px;
    border-radius: 18px;
    color: rgba(248, 250, 252, 0.7);
    font-size: 0.7rem;
    font-weight: 600;
    text-decoration: none;
    position: relative;
    transition: color 0.2s ease, background 0.2s ease, transform 0.2s ease;
  }

  .bottom-nav-item span {
    font-size: 0.7rem;
    letter-spacing: 0.04em;
  }

  .bottom-nav-item svg {
    width: 22px;
    height: 22px;
  }

  .bottom-nav-item.active {
    color: #ecfeff;
    background: rgba(59, 130, 246, 0.15);
    box-shadow: inset 0 0 0 1px rgba(59, 130, 246, 0.35);
  }

  .bottom-nav-item.active::after {
    content: '';
    position: absolute;
    top: 6px;
    width: 26px;
    height: 3px;
    border-radius: 999px;
    background: linear-gradient(135deg, #0ea5e9, #6366f1);
  }

  /* Forms */
  input:not([type="checkbox"]):not([type="radio"]),
  textarea,
  select {
    width: 100%;
    font-size: 16px;
    border-radius: var(--mobile-radius-md);
    border: 1px solid rgba(148, 163, 184, 0.45);
    padding: 0.875rem 1rem;
    background: rgba(255, 255, 255, 0.96);
    transition: border-color var(--mobile-transition), box-shadow var(--mobile-transition);
  }

  input:focus,
  textarea:focus,
  select:focus {
    border-color: rgba(59, 130, 246, 0.65);
    box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.18);
    outline: none;
  }

  .form-row-mobile {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .form-row-mobile > * {
    width: 100%;
  }

  /* Modals */
  .modal-backdrop-mobile {
    position: fixed;
    inset: 0;
    z-index: 110;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding: 0;
    background: rgba(15, 23, 42, 0.55);
    backdrop-filter: blur(16px);
    animation: fadeIn 180ms ease forwards;
  }

  .modal-bottom-sheet {
    width: 100%;
    max-width: 640px;
    background: #ffffff;
    border-top-left-radius: 28px;
    border-top-right-radius: 28px;
    box-shadow: 0 -24px 48px rgba(15, 23, 42, 0.24);
    transform: translateY(8%);
    animation: mobileSheetIn var(--mobile-transition) forwards;
    overflow: hidden;
    max-height: min(92vh, 92dvh);
    display: flex;
    flex-direction: column;
  }

  .modal-handle {
    width: 48px;
    height: 5px;
    border-radius: 999px;
    background: rgba(148, 163, 184, 0.45);
    margin: 0.75rem auto 0.5rem;
  }

  .modal-header-mobile {
    padding: 1.25rem clamp(1.25rem, 5vw, 1.75rem);
    border-bottom: 1px solid rgba(226, 232, 240, 0.7);
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
  }

  .modal-body-mobile {
    flex: 1;
    overflow-y: auto;
    padding: 1.25rem clamp(1.25rem, 5vw, 1.75rem) 1.5rem;
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
  }

  .modal-body-mobile::-webkit-scrollbar {
    width: 0;
    height: 0;
  }

  .modal-footer-mobile {
    padding: 1.25rem clamp(1.25rem, 5vw, 1.75rem);
    border-top: 1px solid rgba(226, 232, 240, 0.7);
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }

  .modal-fullscreen {
    width: 100%;
    height: 100%;
    border-radius: 0;
  }

  .modal-mobile {
    border-radius: var(--mobile-radius-xl);
    box-shadow: var(--mobile-shadow-soft);
  }

  /* Kanban */
  .kanban-container {
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 1.25rem;
  }

  .kanban-container::-webkit-scrollbar {
    display: none;
  }

  .kanban-board {
    display: flex;
    gap: 1rem;
    width: max-content;
    padding-right: 1rem;
  }

  .kanban-column {
    width: min(280px, 78vw);
    min-width: min(280px, 78vw);
    border-radius: var(--mobile-radius-lg);
    background: rgba(255, 255, 255, 0.95);
    box-shadow: var(--mobile-shadow-soft);
    padding: 1rem;
  }

  .kanban-card {
    border-radius: var(--mobile-radius-md);
    box-shadow: 0 12px 24px rgba(15, 23, 42, 0.1);
  }

  /* Waste management */
  .waste-page {
    width: 100%;
  }

  .waste-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .waste-card {
    border-radius: var(--mobile-radius-lg);
  }

  /* Tables */
  .table-wrapper,
  table {
    width: 100%;
    overflow-x: auto;
    display: block;
  }

  table {
    border-collapse: collapse;
    min-width: 100%;
  }

  th,
  td {
    padding: 0.75rem 0.5rem;
    font-size: 0.82rem;
    white-space: nowrap;
  }

  /* Toastify */
  .Toastify__toast-container {
    top: calc(var(--safe-area-top) + 1rem);
    bottom: auto;
    width: calc(100vw - 2rem);
    left: 50%;
    transform: translateX(-50%);
  }

  .Toastify__toast {
    border-radius: var(--mobile-radius-lg);
  }

  /* Dropdowns & tabs */
  .dropdown-menu,
  [role='menu'] {
    position: fixed;
    max-width: calc(100vw - 2rem);
    max-height: 60vh;
    overflow-y: auto;
    z-index: 120;
    border-radius: var(--mobile-radius-lg);
    box-shadow: var(--mobile-shadow-strong);
  }

  .tabs {
    display: flex;
    gap: 0.5rem;
    overflow-x: auto;
    padding: 0.5rem clamp(0.75rem, 4vw, 1rem);
    -webkit-overflow-scrolling: touch;
  }

  .tabs::-webkit-scrollbar {
    display: none;
  }

  .tab {
    flex: 0 0 auto;
    padding: 0.65rem 1rem;
    border-radius: var(--mobile-radius-sm);
    font-size: 0.85rem;
    font-weight: 600;
    background: rgba(148, 163, 184, 0.16);
    transition: background var(--mobile-transition), color var(--mobile-transition);
  }

  .tab.active {
    background: rgba(59, 130, 246, 0.16);
    color: #1d4ed8;
  }

  /* Dashboard spacing */
  .dashboard,
  .dashboard-grid {
    width: 100%;
  }

  .dashboard-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  /* Utilities */
  .safe-top {
    padding-top: var(--safe-area-top);
  }

  .safe-bottom {
    padding-bottom: var(--safe-area-bottom);
  }

  .safe-left {
    padding-left: var(--safe-area-left);
  }

  .safe-right {
    padding-right: var(--safe-area-right);
  }

  .mt-safe-top {
    margin-top: var(--safe-area-top);
  }
}

@media (min-width: 769px) {
  .bottom-nav-mobile {
    display: none !important;
  }
}

@keyframes mobileSheetIn {
  from {
    transform: translateY(12%);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Additional modal fixes for EventDetailsModal and other modals */
@media (max-width: 768px) {
  .modal-backdrop-mobile,
  .modal-backdrop-mobile > div {
    background-color: #ffffff !important;
  }

  .modal-bottom-sheet {
    background-color: #ffffff !important;
    opacity: 1 !important;
    border-radius: 20px 20px 0 0 !important;
    max-height: 90vh !important;
    overflow-y: auto !important;
  }

  .modal-header-mobile,
  .modal-body-mobile {
    background-color: #ffffff !important;
  }

  .modal-handle {
    width: 40px;
    height: 4px;
    background: #cbd5e1;
    border-radius: 2px;
    margin: 0.75rem auto 0.5rem;
    display: block;
  }

  /* Fix iOS bottom sheet animation */
  @supports (-webkit-touch-callout: none) {
    .modal-bottom-sheet {
      transform: translate3d(0, 0, 0);
    }
  }
}
